html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #1b1b1b;
  font-size: 1.4rem;
  text-align: justify;
}
@media (min-width: 768px) {
  p {
    font-size: 1.8rem;
  }
}

.contenedor {
  width: 90%;
  max-width: 130rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3, h4 {
  font-family: "Lato", sans-serif;
  margin: 1.25rem 0;
  font-weight: 400;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenido-centrado {
  max-width: 800px;
}

.sombra {
  -webkit-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 12px 26px 8px rgba(0, 0, 0, 0.1);
}

.boton-rojo {
  background-color: #fc4646c9;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  transition: all 0.6s ease;
}
.boton-rojo:hover {
  cursor: pointer;
}
.boton-rojo:hover {
  background-color: #a30000ee;
  color: #f1f0f0;
}

.boton-amarillo {
  background-color: #f1e420ea;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  transition: all 0.6s ease;
}
.boton-amarillo:hover {
  cursor: pointer;
}
.boton-amarillo:hover {
  background-color: #fdc200fd;
  color: #f1f0f0;
}

.boton-verde {
  background-color: #38e455c9;
  border: none;
