  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  transition: all 0.6s ease;
}
.boton-verde:hover {
  cursor: pointer;
}
.boton-verde:hover {
  background-color: #05a820e8;
  color: #f1f0f0;
}

.boton-azul {
  background-color: #7386d5;
  border: none;
