  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  color: #FFFFFF;
}
.boton-azul:hover {
  cursor: pointer;
}

.boton-volver {
  background-color: #f1e420ea;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  font-size: 2rem;
  height: auto;
  width: auto;
  color: #1b1b1b;
  transition: all 0.6s ease;
}
.boton-volver:hover {
  cursor: pointer;
}
.boton-volver:hover {
  background-color: #fdc200fd;
  color: #f1f0f0;
}

.boton-aumento {
  background-color: #7386d5;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  color: #1b1b1b;
}
.boton-aumento:hover {
  cursor: pointer;
}
.boton-aumento:focus {
  background-color: #0003b6c7;
}

.boton-tipo {
  background-color: #7386d5;
  border: none;
  color: #1b1b1b;
  border-radius: 0.5rem;
  font-weight: 700;
  text-decoration: none;
  padding: 0.5rem 1rem;
  text-align: center;
  font-size: 1.2rem;
  display: inline;
  width: auto;
  height: 3rem;
  color: #1b1b1b;
}
.boton-tipo:hover {
  cursor: pointer;
}
.boton-tipo:focus {
  background-color: #0003b6c7;
}

.boton-superior {
  background-color: #f1e420ea;
  width: 12rem;
  height: 12rem;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.6s ease;
}
.boton-superior:hover {
  background-color: #fdc200fd;
  color: #f1f0f0;
}
.boton-superior:hover {
  transform: scale(1.15);
}

.opcion-superior {
  margin: 2rem auto;
  display: flex;
  justify-content: flex-end;
}
.opcion-superior a img {
  height: 60%;
  width: 60%;
  margin: auto;
}

.plantilla {
  display: flex;
  flex-direction: column;
}

.tabla {
  border: 1px solid #9fa4a7;
  border-radius: 0.5rem;
  background-color: #f1f0f0;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.tabla table {
  background-color: #FFFFFF;
  width: 98%;
  margin: 2rem auto;
}
.tabla table th, .tabla table td {
  border: 1px solid #9fa4a7;
  text-align: center;
  padding: 0.5rem 1rem;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .tabla table th, .tabla table td {
    font-size: 1.6rem;
  }
}

.comision {
  background-color: #38e455c9 !important;
}

.leyenda-representante {
  background-color: #7386d5;
}

.leyenda-vendedor {
  background-color: #38e455c9;
}

/*
    DEMO STYLE
*/
a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.5s;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#sidebar {
  min-width: 25%;
  max-width: 25%;
  background: #7386d5;
  color: #fff;
  transition: all 0.3s;
}
#sidebar ul li a {
  text-align: center;
  padding: 0.4rem;
  font-size: 0.8em;
  display: block;
}
@media (min-width: 768px) {
  #sidebar ul li a {
    padding: 1rem;
    font-size: 1.6rem;
  }
}

#sidebar.active {
  margin-left: -25%;
}

#sidebar .sidebar-header {
  padding: 1rem;
  background: #6d7fcc;
}
#sidebar .sidebar-header h3 {
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  #sidebar .sidebar-header h3 {
    font-size: 2.4rem;
  }
}
#sidebar ul.components {
  padding: 1rem 0;
  border-bottom: 1px solid #47748b;
}

#sidebar ul li a:hover {
  color: #7386D5;
  background: #fff;
}

#sidebar ul li.active > a,
a[aria-expanded=true] {
  color: #fff;
  background: #6d7fcc;
}

a[data-toggle=collapse] {
  position: relative;
}

.dropdown-toggle::after {
  display: block;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}

ul ul a {
  font-size: 0.9em !important;
  padding-left: 30px !important;
  background: #6d7fcc;
}

a.download {
  background: #fff;
  color: #7386D5;
}

a.article,
a.article:hover {
  background: #6d7fcc !important;
  color: #fff !important;
}

/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */
#content {
  width: 100%;
  padding: 1rem;
  min-height: 100vh;
  transition: all 0.3s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */
@media (max-width: 768px) {
  #sidebar {
    margin-left: -25%;
  }

  #sidebar.active {
    margin-left: 0;
  }

  #sidebarCollapse span {
    display: none;
  }
}
.btn-info {
  width: 6rem;
  height: 4rem;
}
.btn-info span {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .btn-info {
    width: 8rem;
  }
}

#sidebarCollapse {
  border-radius: 0.5rem;
  padding: auto;
  background-color: #7386d5;
}
#sidebarCollapse span {
  margin: 0;
}

.descargar {
  height: auto;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .descargar {
    margin: 1rem 2rem;
  }
}

.formulario fieldset {
  margin: 1rem auto;
  background-color: #f1f0f0;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
}
.formulario fieldset legend {
  font-weight: 700;
}
.formulario fieldset input {
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  border: none;
}
.formulario fieldset label, .formulario fieldset input {
  width: 90%;
  margin: 0.5rem auto;
}
.formulario fieldset .opciones {
  display: grid;
  display: grid;
  gap: 0.2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .formulario fieldset .opciones {
    display: grid;
    gap: 0.2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.formulario button {
  width: auto;
  height: auto;
  padding: 1rem;
  font-size: 2rem;
  display: block;
  margin: auto;
}

form fieldset.seccion {
  display: none;
}

.mostrar-seccion {
  display: flex !important;
}

.tabs {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
.tabs button {
  width: auto;
  height: 5rem;
  font-size: auto;
  border: none;
  border-radius: 0.5rem;
  transition: all 350ms ease;
  font-size: 1.4rem;
}
.tabs button:hover {
  background-color: #0003b6c7;
  color: #FFFFFF;
}

.actual {
  background-color: #0003b6c7;
  color: #FFFFFF !important;
}

.agenda {
  border: 1px solid #9fa4a7;
  background-color: #f1f0f0;
  padding: 2rem 4rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.agenda .campo {
  display: flex;
  flex-direction: column;
}

.filtro {
  margin: 2rem auto;
}
.filtro select, .filtro button {
  font-size: 1.4rem;
  width: auto;
  margin: 0.5rem auto;
  border-radius: 1rem;
  border: none;
  padding: 0.6rem 0;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  background-color: #7386d5;
  transition: all 0.3s;
}
.filtro select option, .filtro button option {
  background-color: #FFFFFF;
  border: none;
  font-size: 1.4rem;
}
.filtro select:hover, .filtro button:hover {
  cursor: pointer;
  color: #1b1b1b;
}
.filtro button {
  width: 6rem;
  padding: auto 2rem;
  font-weight: 400;
}

.trabajadores {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
}
.trabajadores .trabajador {
  padding: 5%;
  border-radius: 0.5rem;
  border: 1px solid #9fa4a7;
  transition: all 300ms ease;
}
.trabajadores .trabajador:hover {
  transform: scale(1.05);
}
.trabajadores .trabajador p {
  text-align: center;
  line-height: 1rem;
  font-size: 1.2rem;
}
@media (min-width: 768px) {
  .trabajadores {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .trabajadores .trabajador p {
    font-size: 1.6rem;
    line-height: 1.2rem;
  }
}

.header {
  background-color: #7386d5;
  height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.header h1 {
  margin: 0 auto;
}
.header .buscador {
  width: 80%;
  text-align: center;
}
.header .buscador input {
  height: 4rem;
}

.inmuebles {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.inmuebles .inmueble {
  border: 2px solid #f1f0f0;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: all 300ms ease;
}
.inmuebles .inmueble img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  margin-bottom: 1rem;
}
.inmuebles .inmueble:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.inmuebles .inmueble .info-inmueble {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.inmuebles .inmueble .info-inmueble .direccion, .inmuebles .inmueble .info-inmueble .precio, .inmuebles .inmueble .info-inmueble .pago, .inmuebles .inmueble .info-inmueble .estado {
  margin: 0 auto;
  text-align: center;
  font-size: 1.4rem;
}
.inmuebles .inmueble .info-inmueble .direccion {
  color: #9fa4a7;
}
.inmuebles .inmueble .info-inmueble .estado {
  color: red;
}
@media (min-width: 1024px) {
  .inmuebles {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.buscador {
  margin: 1rem auto;
}

.beneficios {
  margin: 1rem 0;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
}
.beneficios .beneficio {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.beneficios .beneficio:last-of-type {
  border-right: none;
}
.beneficios .beneficio img {
  height: 35%;
  width: 35%;
  margin: auto;
  border-radius: 0;
}
.beneficios .beneficio p {
  font-size: 1.2rem;
  margin: auto 0.8rem;
}

.precio {
  color: #38e455c9;
}

.opciones {
  margin-top: 1.4rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(2, 1fr);
}
.opciones a {
  font-size: 1rem;
  width: 100%;
  height: auto;
  margin: auto;
  color: #1b1b1b;
}
.opciones a:hover {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .opciones a {
    font-size: 1.4rem;
  }
}

.pre-imagenes {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  margin: 2rem 0;
}
.pre-imagenes #primer-imagen {
  grid-column-start: 1;
  grid-row-start: 1;
  grid-column-end: 3;
