  color: #FFFFFF;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.testimonial blockquote {
  padding-left: 4rem;
  position: relative;
  text-align: center;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 3rem;
  height: 5rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
  margin: 0 1rem;
}
@media (min-width: 768px) {
  .testimonial {
    font-size: 1.6rem;
    margin: 4rem;
  }
}

@media (min-width: 1024px) {
  .testimoniales {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}